.header{
  display: flex;
  background: white;
  padding: 30px 30px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  &__page-title{
    font-weight: 700;
    font-size: 25px;
    line-height: 120%;
    text-transform: uppercase;
    color: #3F3F3F;
  }
}
.main{
  display: flex;
  justify-content: center;
  padding-top: 20px;
  background: #EFEFF2;
  &__form{
    display: flex;
    &-input{
      background: #FFFFFF;
      border-radius: 4px;
      padding: 10px 20px;
      width: 585px;
      &::placeholder{
        font-weight: 400;
        font-size: 14px;
        line-height: 120%;
        color: #C2C2C5;
      }
    }
    &-button{
      text-transform: uppercase;
      background: #3F3F3F;
      border-radius: 0px 4px 4px 0px;
      padding: 10px;
      font-size: 14px;
      line-height: 120%;
      color: #FFFFFF;
    }
  }
}
.user{
  margin-top: 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
  &__card{
    display: flex;
    padding: 13px 31px;
    background: #FFFFFF;
    border-radius: 6px;
    width: fit-content;
    &-infoTxt{
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
      /* identical to box height, or 17px */


      color: #3F3F3F;
      span{
        font-weight: 400;
      }
    }
    &-left{
      display: flex;
      flex-direction: column;
      gap: 3px;
    }
    &-right{
      display: flex;
        flex-direction: column;
      gap: 3px;
      padding: 10px;
    }
    &-img{
      border: 1px solid #3F3F3F;
      border-radius: 6px;
      max-width: 100px;
      max-height: 100px;
    }
    &-button{
      padding: 10px;
      background: #3F3F3F;
      border-radius: 6px;
      color: white;
      text-transform: uppercase;
    }
  }
}
